.menu {
    display: flex;
    margin: 50px 20% 0 20%;
    height: 40%;
    background-color: #fff;
    border-radius: 20px;
    padding: 10px 20px;
    justify-content: space-between;
}

.list {
    display: inline-block;
    margin: 0 10px;
    padding: 10px 0;
    color: black;
    text-decoration: none;
}

.list:hover {
    text-decoration: underline;
}

.focused {
    display: inline-block;
    margin: 0 10px;
    padding: 10px 0;
    color: #5B99A6;
    font-weight: bold;
    text-decoration: none;
}

.focused:hover {
    text-decoration: underline;
}


@media screen and (max-width:861px){
    .menu {
        margin: 30px 5% 0 5%;
    }
    .list {
        font-size: 10px;
        margin: 0 5px;
        padding: 5px 0;
    }
    .focused {
        font-size: 10px;
        margin: 0 5px;
        padding: 5px 0;
    }
}